.page-about .profile .image {
    margin-bottom: 20px;
}
.page-about .profile img {
  width: 100%;
  max-height: 300px;
  object-fit: cover;
  object-position: 50% 15%;
  border-radius: 15px;
  box-shadow: rgba(0, 0, 0, 0.15) 0px 2px 8px;
}

.page-about .profile .text p {
  text-indent: 24px;
  line-height: 24px;
  margin-bottom: 10px;
}
.page-about .profile .main-text {
  margin-bottom: 20px;
}

.page-about .profile .quote-text {
  color: #7D7D7D;
  text-align: right;
}
.page-about .profile .quote-text blockquote{
  font-family: 'Times New Roman', Times, serif;
  font-style: italic;
}
.page-about .profile .quote-text blockquote::before{
  content: open-quote;
}
.page-about .profile .quote-text blockquote::after{
  content: close-quote;
}


.page-about .mini-album {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  gap: 10px;
}

/* EricMartinQLZ */
.page-about .mini-album img.photo{
  width: calc(50% - 5px);
  height: 100px;
  background: #C4C4C4;
  border-radius: 15px;
  object-fit: cover;
  object-position: center;
  box-shadow: rgba(0, 0, 0, 0.15) 0px 2px 8px;
}

.page-about .biodata h3 {
  text-align: center;
  font-size: 24px;
  margin-bottom: 20px;
}

.page-about .biodata tr{ 
  display: flex;
  flex-direction: column;
  margin-bottom: 15px;
}

.page-about .biodata th{ 
  text-align: left;
  font-size: 20px;
}
.page-about .biodata th:nth-child(2){ 
  display: none;
}

.page-about .biodata td{ 
  font-size: 18px;
}

.page-about .biodata ul{ 
  padding-left: 20px;
}


/* Responsive */
@media screen and (min-width: 560px) {
  .page-about .mini-album img.photo{
    width: calc(33% - 6px);
  }
}

@media screen and (min-width: 768px) {
  .page-about .profile {
    display: flex;
    gap: 50px;
    margin-bottom: 31.11px;
  }

  .page-about .profile .image {
    order: 2;
    flex: 1;
    display: flex;
    justify-content: center;
    margin-bottom: unset;
  }
  .page-about .profile img {
    height: 300px;
  }

  .page-about .profile .text  {
    order: 1;
    width: 60%;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
  }
  .page-about .profile .main-text {
    margin-bottom: unset;
  }
  .page-about .profile .quote-text {
    margin: auto 0;
  }
  .page-about .profile .quote-text blockquote{
    font-size: 18px;
  }

  .page-about .mini-album {
    justify-content: space-between;
  }
  .page-about .mini-album img.photo{
    width: 133.33px;
  }

  
  .page-about .biodata h3 {
    text-align: left;
    font-size: 28px;
    margin-bottom: 10px;
  }
  
  .page-about .biodata tr{ 
    display: inherit;
    margin-bottom: 15px;
    vertical-align: baseline;
  }
  
  .page-about .biodata th{ 
    text-align: left;
    font-size: 20px;
  }
  .page-about .biodata th:nth-child(1){ 
    padding-right: 15px;
  }
  .page-about .biodata th:nth-child(2){ 
    display: inherit;
  }
  
  .page-about .biodata td{ 
    font-size: 18px;
  }  
}

@media screen and (min-width: 900px) {
  .page-about .profile {
    gap: 75px;
  }

  .page-about .profile img {
    max-width: 250px;
  }
}